import ColorGenerator from "../color/ColorGenerator";
import Color from "../color/Color";
import Particle from './Particle';
import Point from './Point';

/** Handles particle behavior and drawing. */
class Paintbrush {
  constructor() {
    this.particles = [];
    
    this.color = new Color(194, 100, 50);
    this.colorGen = new ColorGenerator(this.particleColor);

    this.settings = {
      size: 4,
      shape: "circle",
      outline: false,

      speed: 1,
      movementOptions: ["creep", "noodle", "crystal", "drip", "bounce", "orbit", "none"],
      movement: "creep",
      growthSpeed: 0,
      
      colorBehaviorOptions: ['global-dynamic', 'global-static', 'local-dynamic'],
      colorBehavior: 'global-dynamic',
      grayscale: false,

      reflectionStyleOptions: ["polar", "horizontal", "vertical", "none"],
      reflectionStyle: "none",
      numReflections: 2, // 1-8
      
      lifespan: -1, // TODO:
      
      interpolateMouseMovements: true,
      interpolateParticleMovements: true,
    };
    this.updates = 0;
  }

  /**
   * Updates particles and draws them on the given 2d canvas context.
   * @param {*} context The canvas context on which to draw.
   * @param {MouseHandler} mouse The mouse handler containing mouse input state.
   * @param {number} width Width of the canvas element.
   * @param {number} height Height of the canvas element.
   */
  updateAndDraw(context, mouse, width, height) {
    if (mouse.pressed) {
      this.addParticle(mouse.position.x, mouse.position.y);
    }

    context.fillStyle = this.color;
    for (let p of this.particles) {
      p.draw(context, this.shape, this.size, !this.outline);
    }

    this.updates += 1;
  }

  addParticle(x, y) {
    const particle = new Particle(x, y);
    this.particles.push(particle);
  }

  draw(canvasContext) {}
}

export default Paintbrush;
